.filter {
  filter: var(--tw-filter);
}
.chart-wrapper {
  width: 100%;
  padding: 0.75rem;
}
.chart-wrapper .chart-content {
  border-radius: 0.375rem;
  padding: 1.5rem;
}
.chart-wrapper .chart-content .page-bar {
  margin-bottom: 0.75rem;
  display: flex;
  justify-content: space-between;
}
.chart-wrapper .chart-content .page-bar .title h1 {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 700;
}

.chart-builder {
  display: flex;
  max-height: 100vh;
  width: 100%;
  flex-direction: row;
}
.chart-builder *,
.chart-builder ::before,
.chart-builder ::after {
  box-sizing: border-box;
  border-width: 0;
  border-style: solid;
  border-color: currentColor;
}
.chart-builder .chart-container {
  flex: 1 1;
  padding: 1.5rem;
}
.chart-builder .builder-settings {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  border-left-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(209, 213, 219, var(--tw-border-opacity));
  padding: 1rem;
  width: 350px;
}
.chart-builder .builder-settings .settings-header {
  margin: 0px;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700;
}
.chart-builder .builder-settings .setting-items {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}
.chart-builder .builder-settings .setting-items .chart-category {
  margin-bottom: 1rem;
}
.chart-builder .builder-settings .setting-items .chart-category .category-title {
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 400;
}
.chart-builder .builder-settings .setting-items .chart-category .chart-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.chart-builder .builder-settings .setting-items .chart-category .chart-list .chart-item {
  cursor: pointer;
  border-radius: 0.375rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(229, 231, 235, var(--tw-border-opacity));
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}
.chart-builder .builder-settings .setting-items .chart-category .chart-list .chart-item:hover {
  --tw-bg-opacity: 1;
  background-color: rgba(229, 231, 235, var(--tw-bg-opacity));
}
.chart-builder .builder-settings .setting-items .collapse .controls {
  max-height: 40vh;
  min-height: 200px;
  overflow-y: auto;
}
.chart-builder .builder-settings .setting-items .setting-controls {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.chart-builder .builder-settings .setting-items .setting-controls .controls-label {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 400;
}
.collapse {
  border-radius: 0.375rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(229, 231, 235, var(--tw-border-opacity));
}
.collapse:not(.open) .title:hover {
  background-color: rgba(156, 163, 175, 0.25);
}
.collapse.open .title .arrow::after {
  transform: rotate(45deg) translateY(-3px);
  -webkit-transform: rotate(45deg) translateY(-3px);
}
.collapse .title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 0.375rem;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 400;
  --tw-text-opacity: 1;
  color: rgba(31, 41, 55, var(--tw-text-opacity));
  cursor: pointer;
}
.collapse .title .arrow::after {
  content: "";
  position: relative;
  display: inline-block;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(107, 114, 128, var(--tw-border-opacity));
  padding: 0.25rem;
  border-width: 0px 1px 1px 0px;
  margin: 1px 0;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transition: transform 0.5s ease;
}
.collapse .controls {
  border-top-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(229, 231, 235, var(--tw-border-opacity));
  padding: 1rem;
}